<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="" prop="mg_state">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChanged(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="getUserFormById(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deletUser(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                @click="setRole(scope.row)"
                type="warning"
                icon="el-icon-setting"
                size="mini"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
        <!-- size-change 
      current-change 
      current-page 
      page-sizes 
      page-size x -->
        <!-- layout:totalx sizes: -->
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="closeDiaglog"
    >
      <!--  -->
      <!--  -->
      <el-form
        :model="userForm"
        :rules="userRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="userForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="userForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="userForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="userForm.mobile"></el-input>
        </el-form-item>
      </el-form>

      <!-- , -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="resetDialogVisible" width="50%">
      <!--  -->
      <!--  -->
      <el-form
        :model="resetuserForm"
        :rules="resetuserRules"
        ref="resetFormRef"
        label-width="70px"
      >
        <el-form-item label="">
          <el-input v-model="resetuserForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="resetuserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="resetuserForm.mobile"></el-input>
        </el-form-item>
      </el-form>

      <!-- , -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetDialogVisible = false"> </el-button>
        <el-button type="primary" @click="resetUser"> </el-button>
      </span>
    </el-dialog>

    <!-- -->
    <el-dialog
      @close="setRoleClose"
      title=""
      :visible.sync="setRoledialogVisible"
      width="50%"
    >
      <div>
        <p>{{ userInfo.username }}</p>
        <p>{{ userInfo.role_name }}</p>
        <p>
          
          <el-select v-model="selectedRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoledialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRole"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>


<script>
export default {
  data() {
    // 
    var checkEmail = (userRules, value, cb) => {
      //
      const regEmail = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;

      if (regEmail.test(value)) {
        return cb();
      }

      cb(new Error(""));
    };
    // 
    var checkMobile = (userRules, value, cb) => {
      //
      const regMobile = /^[1][3,4,5,7,8,9][0-9]{9}$/;

      if (regMobile.test(value)) {
        return cb();
      }

      cb(new Error(""));
    };
    return {
      //
      setRoledialogVisible: false,
      //
      userInfo: {},
      //id
      selectedRoleId: "",
      //
      rolesList: [],
      //
      resetDialogVisible: false,
      resetuserForm: {
        username: "",
        role_id: "",
        email: "",
        mobile: "",
      },

      //
      queryInfo: {
        query: "",
        pagenum: 1, //
        pagesize: 2, //
      },
      userlist: [],
      total: 0,
      //
      addDialogVisible: false,
      userForm: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      userRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: " 6  15 ",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
      resetuserRules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    //id
    async deletUser(id) {
      const confirmInfo = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);

      //confirm, cancel
      if (confirmInfo !== "confirm") return this.$message.info("");
      else {
        //
        const { data: res } = await this.$http.delete("users/" + id);

        if (res.meta.status !== 200)
          return this.$message.error("");
        else {
          this.getUserList();
          this.$message.success("");
        }
      }
    },
    //id
    async getUserFormById(id) {
      this.resetDialogVisible = true;
      const { data: res } = await this.$http.get("users/" + id);
      if (res.meta.status != 200)
        return this.$message.error("id");
      else {
        this.resetuserForm = res.data;
      }
    },
    //
    resetUser() {
      this.$refs.resetFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        else {
          //
          const { data: res } = await this.$http.put(
            "users/" + this.resetuserForm.id,
            {
              email: this.resetuserForm.email,
              mobile: this.resetuserForm.mobile,
            }
          );
          // console.log(res);
          if (res.meta.status != 200)
            return this.$message.error("");
          else {
            this.$message.success("");
            this.resetDialogVisible = false;
            this.getUserList();
          }
        }
      });
    },
    //
    closeDiaglog() {
      this.$refs.addFormRef.resetFields();
    },
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        //
        const { data: res } = await this.$http.post("users", this.userForm);
        // console.log(res);
        if (res.meta.status !== 201) {
          this.$message.error("");
        } else this.$message.success("");

        //
        this.addDialogVisible = false;
        //
        this.getUserList();
      });
    },

    async getUserList() {
      const { data: res } = await this.$http.get("users", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.userlist = res.data.users;
      this.total = res.data.total;
      // console.log(res);
    },
    // pagesize
    handleSizeChange(newSize) {
      // console.log(newSize);
      this.queryInfo.pagesize = newSize;
      //
      this.getUserList();
    },
    // 
    handleCurrentChange(newPage) {
      // console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    // switch
    async userStateChanged(userinfo) {
      // ``
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      );
      if (res.meta.status != 200) {
        //,
        userinfo.mg_state = !userinfo.mg_state;
        return this.$message.error("");
      }
      this.$message.success("");
    },
    //
    async setRole(userinfo) {
      this.userInfo = userinfo;
      //
      const { data: res } = await this.$http.get("roles");
      if (res.meta.status != 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.rolesList = res.data;
      this.setRoledialogVisible = true;
    },
    //
    async saveRole() {
      if (!this.selectedRoleId) {
        return this.$message.error("");
      }
      // console.log(this.userInfo.id);
      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        {
          rid: this.selectedRoleId,
        }
      );
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.$message.success("");
      this.setRoledialogVisible = false;

      this.getUserList();
    },
    setRoleClose() {
      this.selectedRoleId = "";
      this.userInfo = {};
    },
  },
};
</script>

<style lang="less" scoped>
.box-card {
  width: 100%x;
}
</style>
