<template>
  <div class="roles">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addClick" class="addrolebtn"
            ></el-button
          >
        </el-col>
      </el-row>

      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!--  -->
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <el-col :span="5">
                <el-tag @close="removeRight(scope.row, item1.id)" closable>{{
                  item1.authName
                }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  :class="[i2 == 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      @close="removeRight(scope.row, item2.id)"
                      closable
                      type="success"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      @close="removeRight(scope.row, item3.id)"
                      closable
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.id)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deleteRole(scope.row.id)"
              ></el-button
            >
            <el-button
              @click="showSetRights(scope.row)"
              size="mini"
              type="warning"
              icon="el-icon-setting"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      @close="addClose"
      title=""
      :visible.sync="adddialogVisible"
      width="50%"
    >
      <el-form
        :model="addruleForm"
        :rules="addruleFormRules"
        ref="addruleFormRef"
        label-width="80px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="addruleForm.roleName"></el-input>
        </el-form-item>

        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addruleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="adddialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      @close="editClose"
      title=""
      :visible.sync="editdialogVisible"
      width="50%"
    >
      <el-form
        :model="editForm"
        :rules="addruleFormRules"
        ref="editruleFormRef"
        label-width="80px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      @close="setRightsClose"
      title=""
      :visible.sync="setrightdialogVisible"
      width="50%"
    >
    <!--  -->
    <!-- prop -->
      <el-tree
        ref="treeRef"
        :default-checked-keys="defKeys"
        default-expand-all
        node-key="id"
        show-checkbox
        :data="rightsList"        
        :props="treeProps"
      ></el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="setrightdialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "roles",
  components: {},
  created() {
    this.getRolesList();
  },
  data() {
    return {
      rolesList: [],
      adddialogVisible: false,
      editdialogVisible: false,
      addruleForm: {
        roleName: "",
        roleDesc: "",
      },
      editForm: {},
      addruleFormRules: {
        roleName: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      setrightdialogVisible: false,
      rightsList: [],
      //
      treeProps: {
          //
        label: "authName",
        //
        children: "children",
      },
      defKeys: [],
      //id
      roleId: "",
    };
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get("roles");
      // console.log(res);

      if (res.meta.status != 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.rolesList = res.data;
    },
    addClick() {
      this.adddialogVisible = true;
    },
    addClose() {
      this.$refs.addruleFormRef.resetFields();
    },
    addRole() {
      this.$refs.addruleFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post("roles", this.addruleForm);
        if (res.meta.status != 201) {
          this.$message.error("");
        }
        this.adddialogVisible = false;
        this.getRolesList();
        this.$message.success("");
      });
    },
    editRole() {
      this.$refs.editruleFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.put(
          "roles/" + this.editForm.roleId,
          { roleName: this.editForm.roleName, roleDesc: this.editForm.roleDesc }
        );
        if (res.meta.status != 200) {
          this.$message.error("");
        }
        this.editdialogVisible = false;
        this.getRolesList();
        this.$message.success("");
      });
    },
    async deleteRole(id) {
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult != "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete("roles/" + id);
      if (res.meta.status != 200) {
        return this.$message.error("");
      }
      this.getRolesList();

      this.$message({
        type: "success",
        message: "!",
      });
    },
    editClose() {
      this.$refs.editruleFormRef.resetFields();
    },
    async showEditDialog(id) {
      const { data: res } = await this.$http.get("roles/" + id);
      // console.log(res);

      if (res.meta.status != 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.editForm = res.data;
      // console.log(this.editForm);

      this.editdialogVisible = true;
    },
    //x
    async removeRight(role, rightId) {
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult != "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      );
      if (res.meta.status != 200) {
        return this.$message.error("");
      }
      //this.getRolesList(),expand
      this.$message.success("!");
      //role
      role.children = res.data;
    },
    //
    async showSetRights(role) {
      // console.log(role);
      //id
      this.roleId = role.id;
      //
      const { data: res } = await this.$http.get("rights/tree");
      if (res.meta.status != 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      //rightsList
      this.rightsList = res.data;
      //id
      this.getLeafKeys(role, this.defKeys);
      this.setrightdialogVisible = true;
    },
    //id,defKeys
    getLeafKeys(node, arr) {
        //nodechildren
      if (!node.children) {
        return arr.push(node.id);
      }
      //forEach() 
      node.children.forEach((item) => {
        this.getLeafKeys(item, arr);
      });
    },
    //defKeys,
    setRightsClose() {
      this.defKeys = [];
    },
    //
    async allotRights() {
      const keys = [
          //...,
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ];
      //join() 
      const idStr = keys.join(",");
      this.setrightdialogVisible = false;
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      );
      if (res.meta.status != 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getRolesList();
    },
  },
};
</script>
<style lang="less" scoped>
.addrolebtn {
  margin-bottom: 15px;
}
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
//
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
